body {
  height: 100vh; width: 100vw;
  background-color: var(--color-1);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  touch-action: manipulation;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.button {
  height: 60px; width: 150px;
  border: solid 5px var(--color-2);
  background-color: var(--color-3);
  color: var(--color-2);
  position: absolute;
  bottom: 100px; left: 100px;
  font-size: 13px;
  font-weight: bold;
}

:root {
  /* color */
  --color-1:#19171b;
  --color-2:#9e8123;
  --color-3:#2f2921;
  --color-4:#563a17;

  /* clop */
  --clip-t-to-b   :inset(   0    0 100%    0); /* 上→下 */
  --clip-b-to-t   :inset(100%    0    0    0); /* 下→上 */
  --clip-l-to-r   :inset(   0 100%    0    0); /* 左→右 */
  --clip-r-to-l   :inset(   0    0    0 100%); /* 右→左 */
  --clip-c-to-tb  :inset(100%    0 100%    0); /* 内→上下 */
  --clip-c-to-lr  :inset(   0 100%    0 100%); /* 内→左右 */
  --clip-c-to-o   :inset( 50%  50%  50%  50%); /* 内→外 */
  --clip-on       :inset(100% 100% 100% 100%); /* 非表示 */
  --clip-off      :inset(   0    0    0    0); /* 全表示 */
  --clip-out-on  :circle(  0%  at  50%  50%); /* 内→外非表示 */
  --clip-out-off :circle(100%  at  50%  50%); /* 内→外全表示 */
}

.main {
  height: 650px; width: 350px;
  position: relative;
  background-color: var(--color-1);
}

@keyframes clip-slide {
  to {
    clip-path: var(--clip-off);
  }
}

@keyframes clip-fade {
  to {
    opacity: 1;
    transform: translateX(0) translateY(0);
  }
}

@keyframes clip-out {
  to {
    clip-path: var(--clip-out-off);
  }
}

.top-1 {
  height: 50px; width: 350px; top: 0px; z-index: 1;
  position: absolute;
  clip-path: var(--clip-l-to-r);
  animation: clip-slide 1s ease-out forwards;

  .a, .b, .c {
    position: absolute;
    top: 10px; 
    border: solid 5px var(--color-2);
    border-image: linear-gradient(90deg, var(--color-2), var(--color-3), var(--color-2)) 1;
  }

  .a {
    height: 35px; width: 220px; left: 60px;
    border-bottom: none;
  }

  .b {
    height: 35px; width: 50px; left: 10px;
    border-top: none;
    border-right: none;
  }

  .c {
    height: 35px; width: 50px; right: 10px;
    border-top: none;
    border-left: none;
  }
}

.left-1 {
  height: 650px; width: 50px; top: 0px; left: 0px; z-index: 2;
