  clip-path: var(--clip-b-to-t);
  animation: clip-slide 1s ease-out forwards;
  
  .a, .b, .c {
    position: absolute;
    left: 10px; 
    border: solid 5px var(--color-2);
  }

  .a {
    height: 520px; width: 35px; top: 60px;
    border-right: none;
    border-image: linear-gradient(180deg, var(--color-2), var(--color-3), var(--color-2)) 1;
  }

  .b {
    height: 50px; width: 35px; top: 10px;
    border-bottom: none;
    border-left: none;
  }

  .c {
